import React from 'react'
import Loadable from 'react-loadable'

import Loader from '../Loader'

import './Table.scss'


const LoadableTable = Loadable({
    loader: () => import('./Table'),
    loading() {
        return <Loader />
    },
    delay: 250,
})

const TableComponent = props => (
    <div className="TableContainer">
        <LoadableTable {...props} />
    </div>
)

export default TableComponent
